<template>
  <div class="chart">
    <div>
      <div id="chart_instance_el"></div>
    </div>
  </div>
</template>

<script>
import {mapState} from "vuex";
export default {
  name: "chart",
  data() {
    return {
      widget: null,
    }
  },
  computed: {
    ...mapState('option',['symbol', 'resolution']),
    ...mapState('app',['locale']),
  },
  watch: {
    symbol() {
      this.initChart()
    },
    resolution() {
      this.initChart()
    },
    locale(){
      this.initChart()
    }
  },
  methods: {
    initChart() {
      // resolution symbol
      let {locale} = this;
      // debugger
      this.widget = new window.TradingView.widget({
        autosize: true,
        height: '100%',
        symbol: 'ETHUSDT',
        interval: 1440,
        timezone: 'Asia/Shanghai',
        theme: 'dark',
        // style: '3',
        locale: locale,
        toolbar_bg: '#f1f3f6',
        enable_publishing: false,
        allow_symbol_change: true,
        container_id: 'chart_instance_el',
        studies: [
          // "MASimple@tv-basicstudies",
          // 'WilliamsAlligator@tv-basicstudies'
        ],
        studies_overrides: {
          "volume.volume.transparency": 100,
          // "volume.volume.color.0": "#141823",
          // "volume.volume.color.1": "#141823",
        },
        overrides: {
          // 'mainSeriesProperties.areaStyle.color1': "#606090",
          // 'mainSeriesProperties.areaStyle.color2': "#01F6F5",
          'mainSeriesProperties.areaStyle.linecolor': "#0094FF",
          // 'mainSeriesProperties.areaStyle.linestyle': '0',
          'mainSeriesProperties.areaStyle.linewidth': '2'
        },
      });
    }
  },
  mounted() {
    this.initChart();
  }
}
</script>

<style lang="less" scoped>
.chart {
  width: 100%;
  // max-width: 1500px;
  height: 100%;
  background-color: #191f2d;
}
</style>