  cursor: pointer;
  position: relative;
  .b-icon {
    height: 38px;
    margin-right: 15px;
  }
  .display {
    font-size: 13px;
  }
  .price {
    font-size: 16px;
  }
  .arrow {
    width: 30px;
    &.show {
      transform: rotate(180deg);
    }
  }
}
.pair-list {
  width: 180px;
  position: absolute;
  top: 70px;
  left: -14px;
  overflow: hidden;
  border-radius: 10px;
  border: 2px solid #FFFFFF;
  z-index: -1;
  background-color: #191f2d;
  transform: translateY(-100%-30px);
  &.show {
    transform: translateY(0);
  }
  .logo{
    display: none;
  }
  .pair {
    &:hover {
      background-color: #242e40;
    }
    &.active {
      background-color: #323b55;
    }
  }
}
.mask{
  z-index: 9;
  display: none;
}
@media screen and (max-width: 1024px) {
  .mask{
    display: block;
  }
  .pair-list{
    width: 50%;
    max-width: 260px;
    height: 100%;
    transform: translateX(-100%);
    position: fixed;
    top: 0;
    left: 0;
    border: none;
    border-radius: 0;
    z-index: 9;
    box-shadow: none;
    .logo{
      display: block;
      img{
        width: 100%;
      }
    }
    &.show {
      transform: translateX(0);
    }
  }
}
@media screen and (max-width: 540px) {
  .pair{
    height: 50px;
  }
}
</style>