<template>
    <div>
          <optionNoPhone v-if="!isPhone"/>
          <optionPhone v-if="isPhone" />
    </div>
</template>

<script>

import optionNoPhone from "./optionNoPhone.vue";
import optionPhone from "./optionPhone.vue";
export default {
  name: "americanOptionIndex",
  components: {
    optionNoPhone,
    optionPhone,
  },
  data() {
    return {
      isPhone:false,
     }
  },
  mounted(){
      if(/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
           this.isPhone =  true;   //手机端
        } else {
           this.isPhone = false;  //电脑端
        }   
    }
 }
     


</script>

