<template>
  <div class="w-fill a-option-silder">
    <div class="fs-18 mt-16"  >{{title}}</div>
    <div class="my-div  mt-16 a-option-height flex flex1 flex-wrap jc-between ai-center">
      <m-slider shadow @change="handleChnage" class="flex1" v-model="currentValue" :min="min" :max="max" />
        <m-input-plane class="fs-18 a-option-height a-option-silder-value flex jc-center">
          <label class="a-option-silder-label" for="cycle" style="    border: 1px solid #ffffff;
    border-radius: 20px;">
            <!-- {{currentValue}} -->
            <input id="cycle"    class="a-option-silder-input myinput" @focus="handleFocus" @blur="handleBlur" type="text" @input="handleInput" v-model="inputValue">
            <span v-if="!focus">{{symbol}}</span>
          </label>
        </m-input-plane>
    </div>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import mInputPlane from "@/components/mInputPlane";
import mSlider from "@/components/mSlider";

function equal(value1, value2) {
  return String(value1) === String(value2);
} // add num and avoid float number

export default {
  name: "OptionSwitch",
  components: {mInputPlane, mSlider},
  computed:{
    ...mapGetters(['language']),
  },
  props: {
    modelValue: {type:[Number, String]},
    title: {type: String, default: ''},
    symbol: {type: String, default: ''},
    max: {type: Number, default: 30},
    min: {type: Number, default: 0},
  },
  watch: {
    modelValue: function (value) {
      if (!equal(value, this.currentValue)) {
        this.currentValue = value;
        this.inputValue = value;
      }
    },
    currentValue: function(value) {
      if (!equal(value, this.modelValue)) {
        this.$emit('update:modelValue', value)
        this.inputValue = value;
      }
    }
  },
  methods: {
    handleChnage(value) {
      this.$emit('change', value)
    },
    handleFocus() {
      this.focus = true
    },
    handleInput(event) {
      const value = Number(event.target.value)
      if (Number.isNaN(value)) {
        event.target.value = this.modelValue
        return
      }
    },
    handleBlur(event) {
      this.focus = false
      const value = Number(event.target.value)
      if (value > this.max) {
        this.$emit('update:modelValue', this.max)
        event.target.value = this.max
        return
      }
      if (value < this.min) {
        this.$emit('update:modelValue', this.min)
        event.target.value = this.min
        return
      }
      this.$emit('update:modelValue',value)
    }

  },
  mounted() {
    if (!equal(this.modelValue, this.currentValue)) {
        this.currentValue = this.modelValue;
        this.inputValue = this.modelValue;
      }
  },
  data(){
    return {
      focus: false,
      currentValue:'',
      inputValue: '',
    }
  },
}
</script>

