   .my-div{
     padding-left:10px;
   }
.a-option-silder{

  // width: calc(100% - 14px);
  max-width: 383px;
  &-value{
    line-height: 55px;
    height: 55px;
    width: 100px;
    text-align: center;
    margin-left: 16px;
  }
  &-input{
    min-width: 10px;
    width: 2em;
    overflow: visible;
    display: inline-block;
    text-align: right;
    &:focus{
      text-align: center;
      width: 100%;
    }
  }
  &-label{
    cursor: pointer;
    width: 100%;
  }
}
</style>