<template>
  <div class="w-fill profit-calculatar">
    <div class="flex ai-center mt-28 jc-between">
      <h2 class="fs-22 text lh-1 pm-0">{{language.aoption_pl_calc}}</h2>
      <h4 class="fs-18 text lh-1 pm-0">{{calcSlider}} %</h4>
    </div>
    <div class="mt-28">
      <m-slider class="flex1" v-model="calcSlider" :min="-50" :max="50" />
    </div>
    <div class="flex jc-between ai-center mt-28">
      <div>
        <div class="">{{language.aoption_pre_price}}ETH</div>
        <div class="mt-16 text fs-18">$ {{ dispalyPrice.toDisplay().myFixed() }}</div>
      </div>
      <div class="ta-right">
        <div class="">{{language.aoption_pre_pure_profit}}</div>
        <div class="mt-16 green fs-18" v-if="displayProfit">${{ profitFn(displayProfit) }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import mSlider from "@/components/mSlider";
import { BIG_ZERO, BN } from "@/common/util";

export default {
  name: "ProfitCalculatar",
  components: {mSlider},
  computed:{
    ...mapGetters(['language']),
    dispalyPrice() {
      return this.price.times(100 + this.calcSlider).div(100)
    },
    displayProfit() {
      // 看涨收益: (预期价格-行权价格)* 数量 - 期权成本
      // 看跌收益: (行权价格 - 预期价格)* 数量 - 期权成本
      if (this.isUp) {
        const upPrice = this.dispalyPrice.minus(this.price).times(this.amount).toDisplay()
        return new BN(upPrice).minus(this.optionPrice.toDisplay('balance')).toString()
      }
      const downPrice = this.price.minus(this.dispalyPrice).times(this.amount).toDisplay()
      return new BN(downPrice).minus(this.optionPrice.toDisplay('balance')).toString()
      // return this.price.minus(this.dispalyPrice).times(this.amount).minus(this.optionPrice)
    },
  },
  props: {
    cycleDay: {type: Number }, // 期限
    price: { type: Object, default: BIG_ZERO },
    optionPrice: { type: Object, default: BIG_ZERO },
    amount: { type: String, default: '0' },
    isUp: { type: Boolean, default: true }
  },
  data(){
    return {
      filterFlag:false,
      searchText:'',
      poolLength:0,
      calcSlider: 0,
      loading:false,
    }
  },
  methods:{
    profitFn(val){
      return  val.substring(0,8)
    },
    // getPoolList(){
    //   this.loading = true;
    //   this.$api.getPoolLength().then(length=>{
    //     this.poolLength = Number(length);
    //     setTimeout(()=>this.loading = false,500);
    //   }).catch(()=>this.loading = false);
    // },
  },
  beforeMount() {
    // this.getPoolList();
  }
}
</script>

