            </div>
          </div>
        </div>
      </m-box>

      <div class="flex-center-lump-26"></div>

      <m-box class="a-option-right phoneTwo">
        <OptionRightTop
          :isUp="type === OptionType.UP"
          :evenPrive="evenPrive"
          :amount="amount"
          :pricePerHour="pricePerHour"
          :coinPrice="coinPrices[coin].price"
          :decimals="coinPrices[coin].decimals"
          :optionPrice="optionPrices[coin]"
        />

        <div
          style="margin-top: 20px"
