
  <template>
  <div class="a-option flex flex-column ai-center page container-full">
    <div class="webblleStatus">
      <devise @onChange="coinHandleChange" :coinPairs="coinsTabs"></devise>
    </div>

    <!-- isPhone   手机端true     isPhone电脑端false -->

    <div
      class="a-option-content flex flex-wrap jc-between"
      style="padding-bottom: 10px"
      v-if="viewType == '0'"
    >
      <m-box class="a-option-left">
        <h2 class="fs-22 text lh-1 pm-0">{{ language.aoption_buy }}</h2>
        <div class="flex flex-wrap jc-between">
          
          <OptionSwitch
            v-if="isPhone"
            style="margin-top:20px;"
            colorBtn
            @onChange="typeHandleChange"
            :title="language.aoption_type"
            :tabs="poolTypeTabs"
          />

          <div class="left-box">
            <div class="left-flex">
              
              <OptionSwitch 
                style="margin-top: 40px;"
                colorBtn
                @onChange="typeHandleChange"
