                :title="language.aoption_limit"
              />

              <OptionSlider
                class="magt-btn"
                @change="getCoinPrice"
                v-model="cycleDay"
                :title="language.aoption_cycle"
                :symbol="language.d"
                :max="30"
                :min="1"
              />

              <!-- <OptionSwitch
                style="margin-top: 20px"
                colorBtn
                @onChange="typeHandleChange"
                :title="language.aoption_type"
                :tabs="poolTypeTabs"
              /> -->

              <m-button
                style="width: 100%; margin-top: 30px"
                :loading="loading"
                @click="approveHandle()"
                v-show="!isApprove"
                class="a-option-trade hdr-el-blue flex1"
                :height="36"
              >
                {{ language.btn_authorize }}USDT
              </m-button>

              <m-button
                style="width: 100%; margin-top: 30px"
                :loading="loading"
                @click="createOption"
                v-show="isApprove"
                class="a-option-trade hdr-el-blue flex1"
                :height="36"
              >
                {{ language.aoption_add_btn }}
              </m-button>

              <div
                style="margin-top: 20px"
                class="mt-16 orange ta-center flex ai-center"
              >
                <img
                  class="tips-small-img"
                  src="../../images/a-option/tips-small.png"
                  alt=""
                />
                <span class="aoption-top">{{
                  language.aoption_max_amount_tips
                }}</span>
              </div>
            </div>

            <div class="right-flex">
              <div class="right-price">
                <div class="tips-color mt-16">
                  {{ language.wallet_balance }}
                  <span @click="hanldeInpputBalance" class="cursor-p">
                    {{ balanceFormatter(currentBalance) }}
                  </span>
                  {{ symbol1 || symbol }}
                </div>
                <div class="tips-color a-option-typevalues">
                  {{ language.aoption_max_amount }}

                  <span v-if="maxCreateNum.isFinite() && maxCreateNum.toDisplay('balance').toFixed(8) > 0 ">
                    {{ maxCreateNum.toDisplay("balance").toFixed(8) }}
                    {{ coin }}
                  </span>

                  <span v-else>
                        0
                    {{ coin }}
                  </span>

                </div>
              </div>

              <div>
                <m-input-plane
                  class="a-option-tips verified"
                  :class="listeningLanguage ? 'verified1' : ''"
                >
                  <!-- {{ coinPrices[coin].decimals }} -->
                  {{
                    t("aoption_submit_tips", {
                      coin,
                      type:
                        type === OptionType.UP
                          ? language.aoption_look_up
                          : language.aoption_look_down,
                      tradePrice: coinPrices[coin].price
                        .toDisplay(coinPrices[coin].price.decimals)
                        .myFixed(),
                      price: optionPrices[coin].toDisplay("balance").myFixed(),
                      cycle: cycleDay,
                      currenPrice: evenPrive,
                      drawPrice: evenPrive,
                    })
                  }}
                </m-input-plane>

                <!-- 自动行权功能 -->
                <div class="">
                  <m-switch
                    class="switch"
                    v-model="hedged"
                    @exerciseFn="exerciseFn"
                    style="margin-top: 20px"
                  >
                    <div class="exer">
                      <div class="text">{{ language.exercise }}</div>
                      <div>{{ language.period }}</div>
                    </div>
                  </m-switch>
                </div>
              </div>
            </div>
          </div>
        </div>
      </m-box>

      <div class="flex-center-lump-26"></div>
      <m-box class="a-option-right">
        <OptionRightTop
          :isUp="type === OptionType.UP"
          :evenPrive="evenPrive"
          :amount="amount"
          :pricePerHour="pricePerHour"
          :coinPrice="coinPrices[coin].price"
          :decimals="coinPrices[coin].decimals"
          :optionPrice="optionPrices[coin]"
        />
        <m-input-plane active class="a-option-tips">
          <div class="flex ai-center my-img">
            <div class="img-box">
              <img
                class="a-option-tips-big"
                src="../../images/a-option/tips-big.png"
                alt=""
              />
            </div>

            <div class="box-text">
              <p>
                {{ language.aoption_tips2 }}{{ language.aoption_tips3
                }}{{ language.aoption_tips4 }}
              </p>
            </div>
          </div>
        </m-input-plane>

        <ProfitCalculatar
          :price="coinPrices[coin].price"
          :optionPrice="optionPrices[coin]"
          :decimals="coinPrices[coin].decimals"
          :cycleDay="cycleDay"
          :amount="amount"
          :isUp="type === OptionType.UP"
        />
      </m-box>
    </div>

    <div class="box down mytype">
      <div class="flex jc-center">
        <p class="chart-notice flex jc-center ai-center">
          <img class="icon-s" src="../../images/option/kline1.png" alt="" />
          {{ language.tip_chart }}
        </p>
      </div>
      <div class="chart-top">
        <chart />
      </div>
    </div>

    <div v-if="loading" class="mask flex jc-center ai-center ta-center">
      <span
        ><i class="icon-loading"></i><br />{{ language.tip_loading }}...</span
      >
    </div>
  </div>
</template>

<script>
import MSwitch from "@/components/mSwitch";
import devise from "./components/devise.vue";

import chart from "./components/chart";
import { balanceFormatter } from "@/common/dataConvertor";
import { mapState, mapActions, mapGetters } from "vuex";
import mBox from "@/components/mBox";
import mButton from "@/components/mButton";
import mInputPlane from "@/components/mInputPlane";
import {
  maxUint256,
  BN,
  OptionCoin,
  OptionType,
  encodeDefxCallName,
  BIG_ZERO,
} from "@/common/util";
import OptionHeader from "./components/OptionHeader";
import OptionSwitch from "./components/OptionSwitch";

import currencyChoice from "./components/currencyChoice";

import OptionSlider from "./components/OptionSlider";
import OptionInputAmount from "./components/OptionInputAmount";
import OptionRightTop from "./components/OptionRightTop";
import ProfitCalculatar from "./components/ProfitCalculatarIndex";
import lookUp from "@/images/option/look-up.png";
import lookDown from "@/images/option/look-down.png";
import { coinsTabs } from "./datas";

const poolTypeTabs = [
  {
    lable: "aoption_look_up",
    type: OptionType.UP,
    color: "green",
    border: "border-green hdr-el-green",
    image: lookUp,
  },
  {
    lable: "aoption_look_down",
    type: OptionType.DOWN,
    color: "red",
    border: "border-red hdr-el-red",
    image: lookDown,
  },
];

export default {
  name: "americanOptionIndex",
  components: {
    MSwitch,
    devise,
    chart,
    OptionHeader,
    mBox,
    mButton,
    OptionSwitch,
    OptionInputAmount,
    mInputPlane,
    OptionSlider,
    OptionRightTop,
    ProfitCalculatar,
    currencyChoice,
  },
  computed: {
    ...mapState("app", ["locale"]),
    ...mapGetters(["language", "t"]),
    ...mapState("app", ["balance", "address"]),
    ...mapState("aoption", ["options", "dataInfo"]),
    isApprove() {
      return this.allowance.gt(0);
    },
    contractName() {
      return encodeDefxCallName(this.type, this.coin);
    },
    currentCoin() {
      return OptionCoin.BASE_SYMBOL;
    },
    currentBalance() {
      return this.balance[this.currentCoin];
    },
    totalCost() {
      return BIG_ZERO;
    },
    poolTypeTabs() {
      return poolTypeTabs.map((item) => {
        return {
          ...item,
          lable: this.language[item.lable],
        };
      });
    },
    // 打平价格
    evenPrive() {
      // 看涨： 打平价格 = 行权价格 +（权利金 /  买入数量）
      // 看跌： 打平价格 = 行权价格 - （权利金 /  买入数量）
      // const value = new BN(amount).toWei('balance').toString(10)
      const coinPrice = this.coinPrices[this.coin].price;
      const optionPrice = this.optionPrices[this.coin];
      if (this.type === OptionType.UP) {
        return new BN(coinPrice)
          .toDisplay()
          .plus(
            new BN(optionPrice).div(
              new BN(this.amount).toWei("balance").toString(10)
            )
          )
          .myFixed();
      }
      return new BN(coinPrice)
        .toDisplay()
        .minus(
          new BN(optionPrice).div(
            new BN(this.amount).toWei("balance").toString(10)
          )
        )
        .myFixed();
    },
    // 每小时成本
    pricePerHour() {
      const optionPrice = this.optionPrices[this.coin];
      return optionPrice
        .div(this.cycleDay * 24)
        .toDisplay("balance")
        .myFixed();
    },
    // 最大可投额度
    maxCreateAmount() {
      const { totalBalance, maxUtilizationRate, lockedAmount } =
        this.options[this.contractName];
      // if (this.type === OptionType.UP) {
      //   return totalBalance.times(maxUtilizationRate).div(100).minus(lockedAmount)
      // }
      return totalBalance
        .times(maxUtilizationRate)
        .div(100)
        .minus(lockedAmount);
      //  pool.balance()* pool.maxUtilizationRate()/100 -   pool.lockedAmount()
      // pool.balance()* pool.maxUtilizationRate()/100 -   pool.lockedAmount()
    },
    // 最大可投数量
    maxCreateNum() {
      const { collateralizationRatio } = this.options[this.contractName];
      if (this.type === OptionType.UP) {
        return this.maxCreateAmount.times(100).div(collateralizationRatio);
      }
      // 可用额度 * 1e10/(pool.collateralizationRatio()* currentPrice())
      console.log(this.coinPrices,collateralizationRatio,this.coinPrices[this.coin].price)
      return this.maxCreateAmount
        .times(1e8)
        .times(100)
        .div(
          new BN(collateralizationRatio).times(this.coinPrices[this.coin].price)
        );
    },
  },
  data() {
    return {
      hedged: false, // 自动行权
      isPhone: false,
      viewType: "0",
      roundIndex: 2,
      touchStyle: {},
      hammer: null,
      statusIndex: "",
      OptionType,
      coin: coinsTabs[0].symbol,
      type: poolTypeTabs[0].type,
      priceContractName: coinsTabs[0].priceContractName,
      coinsTabs,
      filterFlag: false,
      searchText: "",
      poolLength: 0,
      loading: false,
      amount: 0.01,
      cycleDay: 1,
      calcSlider: 12,
      allowance: new BN(0),
      coinPrices: (() => {
        const resval = {};
        coinsTabs.forEach((item) => {
          resval[item.symbol] = {
            price: new BN(0),
            decimals: 8,
          };
        });
        return resval;
      })(),
      optionPrices: (() => {
        const resval = {};
        coinsTabs.forEach((item) => {
          resval[item.symbol] = new BN(0);
        });
        return resval;
      })(),
    };
  },
  watch: {
    address(value) {
      console.log(value, "=========]");
      this.getDataInfo();
    },
    locale(newName) {
      if (
        newName == "de_DE" ||
        newName == "fr" ||
        newName == "es" ||
        newName == "en"
      ) {
        this.listeningLanguage = true;
      } else {
        this.listeningLanguage = false;
      }
    },
  },
  beforeMount() {
    if (/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
      this.isPhone = true; //手机端
    } else {
      this.isPhone = false; //电脑端
    }

    this.whetherExerciseFn(); //是否行权
    this.getCoinPrice();
    this.getDataInfo();
    // 获取合约基础数据
    this.getActivePoolInfo(encodeDefxCallName(poolTypeTabs[0].type, coinsTabs[0].symbol)); // 看涨 ETH
    this.getActivePoolInfo(encodeDefxCallName(poolTypeTabs[1].type, coinsTabs[0].symbol)); // 看跌 ETH
    // this.getActivePoolInfo(encodeDefxCallName(poolTypeTabs[0].type, coinsTabs[1].symbol));
    // this.getActivePoolInfo(encodeDefxCallName(poolTypeTabs[1].type, coinsTabs[1].symbol));
    let newName = this.locale;
    if (
      newName == "de_DE" ||
      newName == "fr" ||
      newName == "es" ||
      newName == "en"
    ) {
      this.listeningLanguage = true;
    }
  },
  methods: {
    balanceFormatter,
    ...mapActions("aoption", ["setOption", "setDataInfo"]),
   _isNaN(val) {
    if (val == "NaN") {
        return 0;
    }
    return val;
  },
    // 点击行权或者关闭
    exerciseFn() {
      this.loading = true;
      this.$api
        .americaOptionAutoExercise(this.hedged)
        .then(() => {
          this.loading = false;
          this.whetherExerciseFn();
        })
        .catch(() => {
          this.loading = false;
          this.whetherExerciseFn();
        });
    },
    //  是否行权
    whetherExerciseFn() {
      this.$api.getAmericaOptionAutoExercise(this.address).then((res) => {
        this.hedged = res;
      });
    },
    coinHandleChange(item) {
      this.coin = item.symbol;
      this.priceContractName = item.priceContractName;
      this.getCoinPrice();
    },
    typeHandleChange(item) {
      this.type = item.type;
      console.log(this.type, OptionType, "this.type ");
    },
    getActivePoolInfo(contractName) {
      this.$api.getDefxCallData(contractName).then((info) => {
        this.setOption({ key: contractName, info });
      });
    },
    getDataInfo() {
      this.$api.getFacadeInfo().then((info) => {
        this.allowance = info.allowance;
      });
    },
    createOption() {
      if (!Number(this.amount)) {
        this.$toast.warning(this.language.aoption_enter_amount);
        return;
      }
      if (this.currentBalance.lt(new BN(this.amount).toWei("balance"))) {
        this.$toast.warning(this.language.label_6_5);
        return;
      }
      this.loading = true;
      /**
       * @dev 购买期权
       * @param contractName 交易合约类型
       * @param period 周期（秒）
       * @param amount 数量
       * @param strike 价格
       */

      const period = this.cycleDay * 86400; // 获取周期 天数 * 一天的总秒数
      this.$api
        .createOptionFacade(
          this.contractName,
          period,
          this.amount,
          this.coinPrices[this.coin].price.toString(),
          this.optionPrices[this.coin].toString()
        )
        .then(() => {
          this.$toast.success(this.language.aoption_add_success);
          this.$api.getBalance();
        })
        .catch((err) => {
          console.error(err);
          if (err?.code !== 4001) {
            this.$toast.error(this.language.aoption_add_success);
          }
        })
        .finally(() => {
          this.loading = false;
        });
    },
    approveHandle() {
      this.loading = true;
      this.$api
        .erc20Approve("Facade", "USDT")
        .then(() => {
          // this.setDataInfo({ allowance: maxUint256 });
          this.allowance = maxUint256;
          this.$toast.success(this.language.aoption_success);
        })
        .catch(() => {
          this.$toast.error(this.language.aoption_fail);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getPoolList() {
      this.loading = true;
      this.$api
        .getPoolLength()
        .then((length) => {
          this.poolLength = Number(length);
          setTimeout(() => (this.loading = false), 500);
        })
        .catch(() => (this.loading = false));
    },
    getCoinPrice() {
      console.log(this.priceContractName, "this.priceContractName");
      this.$api.getLatestPrice(this.priceContractName).then((res) => {
        this.coinPrices[this.coin].price = new BN(res.answer);
        this.coinPrices[this.coin].decimals = Number(res.decimals);
        this.getOptionFacadePrice();
      });
    },
    getOptionFacadePrice() {
      const period = this.cycleDay * 86400; // 获取周期 天数 * 一天的总秒数
      this.$api
        .getOptionFacadePrice(
          this.contractName,
          period,
          this.amount,
          this.coinPrices[this.coin].price.toString()
        )
        .then((res) => {
          console.log(res);
          this.optionPrices[this.coin] = new BN(res.total);
        });
    },
  },
};
</script>

